    ta_salamon: "சாலமன் பாப்பையா : முதுமையில் செய்யலாம் என எண்ணாமல் இப்போதே அறத்தைச் செய்க; அந்த அறம் நாம் அழியும் போது தான் அழியாமல் நமக்கு துணை ஆகும்.",
    en: "Defer not virtue to another day; receive her now; and at the dying hour she will be your undying friend."
