    ta_salamon: "சாலமன் பாப்பையா : அறத்துடன் வருவதே இன்பம்; பிற வழிகளில் வருவன துன்பமே; புகழும் ஆகா",
    en: "Only that pleasure which flows from domestic virtue is pleasure; all else is not pleasure, and it is without praise."
