    ta_salamon: "சாலமன் பாப்பையா : தனக்குத் தீமை செய்தவரைப் பொறுக்காமல் தண்டித்தவரைப் பெரியோர் ஒரு பொருட்டாக மதிக்கமாட்டார்; பொறுத்துக் கொண்டவரையோ பொன்னாகக் கருதி மதிப்பர்.",
    en: "(The wise) will not at all esteem the resentful. They will esteem the patient just as the gold which they lay up with care."
