    ta_salamon: "சாலமன் பாப்பையா : கூடி மகிழுமாறு இனியன பேசி நட்பை வளர்க்கத் தெரியாதவர், புறம்பேசி நண்பர்களையும் பிரித்து விடுவர்.",
    en: "Those who know not to live in friendship with amusing conversation will by back-biting estrange even their relatives."
