    ta_salamon: "சாலமன் பாப்பையா : தன்னோடு நெருக்கமானவர்களின் குற்றத்தையும் அவர் இல்லாத நேரம் பேசும் இயல்புடையவர்கள், அயலார் காரியத்தில் என்னதான் பேசமாட்டார்கள்!",
    en: "What will those not do to strangers whose nature leads them to publish abroad the faults of their intimate friends ?"
