    ta_salamon: "சாலமன் பாப்பையா : புறம்பேச அடுத்தவர் குற்றத்தைப் பார்ப்பவர், பேசும் தம் குற்றத்தையும் எண்ணினால், நிலைத்து இருக்கும் உயிர்க்குத் துன்பமும் வருமோ?",
    en: "If they observed their own faults as they observe the faults of others, would any evil happen to men ?"
