    ta_salamon: "சாலமன் பாப்பையா : பலரும் கேட்டு வெறுக்கப், பயனற்ற சொற்களைச் சொல்லுபவன் எல்லாராலும் இகழப்படுவான்.",
    en: "He who to the disgust of many speaks useless things will be despised by all."
