    ta_salamon: "சாலமன் பாப்பையா : செல்வங்கள் பலவற்றுள்ளும் சிறந்தது அருள் என்னும் செல்வமே. பொருட்செல்வம் இழிந்த மனிதரிடமும் உண்டு.",
    en: "The wealth of kindness is wealth of wealth, in as much as the wealth of property is possessed by the basest of men."
