    ta_salamon: "சாலமன் பாப்பையா : நல்லநெறியில் வாழ்ந்து, நமக்கு உதவும் அறம் எது என ஆய்ந்து, அருளுடன் வாழ்க; எல்லாச் சமய நெறிகளால் ஆய்ந்தாலும் அருளே நமக்குத் துணையாகும்.",
    en: "Stand) in the good path, consider, and be kind. Even considering according to the conflicting tenets of the different sects, kindness will be your best aid, (in the acquisition of heavenly bliss.)"
