    ta_salamon: "சாலமன் பாப்பையா : தன் உடம்பை வளர்ப்பதற்காக இன்னோர் உடம்பைத் தின்பவன் மனத்துள் இரக்கம் எப்படி இருக்கும்?",
    en: "How can he be possessed of kindness, who to increase his own flesh, eats the flesh of other creatures."
