    ta_salamon: "சாலமன் பாப்பையா : இறைச்சியைத் தின்னாது இருத்தல் என்னும் அறத்தின்மேல் உயிர்நிலை இருக்கிறது. இதை மீறித் தின்னும் உயிர்களை நரகம் விழுங்கும்; வெளியே விடவும் செய்யாது.",
