    ta_salamon: "சாலமன் பாப்பையா : தின்பதற்காகவே கொலை செய்பவர் இல்லை என்றால், இறைச்சியை விலைக்குத் தருபவரும் உலகில் எங்கும் இருக்கமாட்டார்.",
    en: "If the world does not destroy life for the purpose of eating, then no one would sell flesh for the sake of money."
