    ta_salamon: "சாலமன் பாப்பையா : இறைச்சி, இன்னோர் உடம்பின் புண்; அறிந்தவர் அதை உண்ணக்கூடாது.",
    en: "If men should come to know that flesh is nothing but the unclean ulcer of a body, let them abstain from eating it."
