    ta_salamon: "சாலமன் பாப்பையா : எந்த உயிரையும் கொல்லாதவனாய், இறைச்சியைத் தின்ன மறுத்தவனாய் வாழ்பவனை எல்லா உயிர்களும் கை குவித்துத் தொழும்.",
    en: "All creatures will join their hands together, and worship him who has never taken away life, nor eaten flesh."
