    ta_salamon: "சாலமன் பாப்பையா : நெருப்பு சுடச்சுடப் பொன்னின் ஒளி பெருகுவது போலத் துன்பம் வருத்த வருத்தத் தவம் செய்பவர்க்கு ஞானம் பெருகும்.",
    en: "Just as gold is purified as heated in the fire, will those shine, who have endured the burning of pain (in frequent austerities)."
