    ta_salamon: "சாலமன் பாப்பையா : வஞ்ச மனத்தவனின் திருட்டு நடத்தையைக் கண்டு அவன் உடம்போடு கலந்து இருக்கும் நிலம், நீர், தீ, காற்று, ஆகாயம் என்னும் ஐந்து பூதங்களும் தமக்குள் சிரிக்கும்.",
    en: "The five elements (of his body) will laugh within him at the feigned conduct of the deceitful minded man."
