    ta_salamon: "சாலமன் பாப்பையா : மேலான நிலையில் இருந்தும் கீழான செயல்களைச் செய்வது, வேட்டை ஆடுபவர் புதருக்குப் பின் மறைந்து நின்று பறவைகளைப் பிடிப்பது போலாம்.",
