    ta_salamon: "சாலமன் பாப்பையா : எத்தகைய பற்றுகளும் இல்லாதவர் என்று வாயால் சொல்லிச் செயலால் தவறாக வாழ்பவரின் வாழ்க்கை, பிறகு ஏன் அப்படிச் செய்தோம் ஏன் அப்படிச் செய்தோம் என்று வருந்தும்படி பல துன்பங்களையும் தரும்.",
