    ta_salamon: "சாலமன் பாப்பையா : மனத்துள் எதையும் வெறுக்காமல், வெளியே வெறுத்தவர் போல் ஏமாற்றி வாழும் மனிதரைக் காட்டிலும் கொடியவர், இவ்வுலகத்தில் இல்லை.",
