    ta_salamon: "சாலமன் பாப்பையா : அடுத்தவர் நம்மை இகழக்கூடாது என்று எண்ணுபவன், அடுத்தவர்க்குரிய எந்தப் பொருளையும் மனத்தால்கூடத் திருட நினைக்கக்கூடாது.",
