    section: "அறத்துப்பால்",
    meaning: {
    ta_mu_va: "மு.வ : யாரிடத்திலும் சினம் கொள்ளாமல் அதை மறந்து விட வேண்டும், தீமையான விளைவுகள் அச் சினத்தாலேயே ஏற்படும்.",
    ta_salamon: "சாலமன் பாப்பையா : தீமை வருவது எல்லாம் கோபத்தால்தான்; அதனால் எவரிடமானாலும் சரி, கோபம் கொள்வதை விட்டுவிடுக.",
    en: "Forget anger towards every one, as fountains of evil spring from it."
    }
