    ta_salamon: "சாலமன் பாப்பையா : முகத்தில் சிரிப்பையும், மனத்துள் மகிழ்ச்சியையும் கொன்றுவிடும் கோபத்தை விட வேறு பகையும் உண்டோ?",
    en: "Is there a greater enemy than anger, which kills both laughter and joy ?"
