    ta_salamon: "சாலமன் பாப்பையா : நாடக அரங்கிற்கு கூட்டம் வருவது போல் சிறுகச் சிறுக செல்வம் சேரும். நாடகம் முடிந்ததும கூட்டம் கலைவது போல் மொத்தமாய்ப் போய்விடும்.",
    en: "The acquisition of wealth is like the gathering together of an assembly for a theatre; its expenditure is like the breaking up of that assembly."
