    ta_salamon: "சாலமன் பாப்பையா : நிலையாத இயல்பினை உடையது செல்வம்; அது கிடைத்தால் நிலையான அறங்களைச் செய்க.",
    en: "Wealth is perishable; let those who obtain it immediately practise those (virtues) which are imperishable."
