    ta_salamon: "சாலமன் பாப்பையா : நேற்று இருந்த ஒருவன் இன்று இல்லை என்று சொல்லும்படி நிலையாமையை உடையது இவ்வுலகம்.",
    en: "This world possesses the greatness that one who yesterday was is not today."
