    ta_salamon: "சாலமன் பாப்பையா : உயிரும் உடம்பும் இணைந்திருந்தும் உ‌டம்பின் நிலையற்ற தன்மையை ஒரு கணப்பொழுதும் அறிய இயலாதவர் கோடிக்கும் மேலான நினைவுகளை எண்ணி நிற்பர்.",
    en: "Innumerable are the thoughts which occupy the mind of (the unwise), who know not that they shall live another moment."
