    ta_salamon: "சாலமன் பாப்பையா : உடம்பிற்கும் உயிருக்கும் இடையேயான உறவு, முட்டை தனித்துக் கிடக்கப் பறவை பறந்து விடுவது போன்றதே.",
    en: "The love of the soul to the body is like (the love of) a bird to its egg which it flies away from and leaves empty."
