    ta_salamon: "சாலமன் பாப்பையா : ஆசைகளைப் பிறப்பிக்கும் ஐந்து புலன்களையும் அடக்க வேண்டும்; அவற்றை அடக்குவதற்குத் தனக்குரிய அனைத்தையும் விட்டு விட வேண்டு்ம்.",
    en: "Let the five senses be destroyed; and at the same time, let everything be abandoned that (the ascetic) has (formerly) desired."
