    ta_salamon: "சாலமன் பாப்பையா : இனியும் பிறப்பது கூடாது என்று பிறப்பை‌யே அறுக்க முயன்றவர்க்கு அவரது உடம்பே அதிகம்; நிலைமை இப்படி இருக்க, உடம்பிற்கும் மேலான சுமை எதற்கு?",
    en: "What means the addition of other things those who are attempting to cut of (future) births, when even their body is too much (for them)."
