    ta_salamon: "சாலமன் பாப்பையா : செவியால் நுகரப்படும் சுவைகளை உணராமல், வாயால் அறியப்படும் சுவைகளை மட்டுமே அறியும் மனிதர் இருந்தால் என்ன? இறந்தால்தான் என்ன?",
