    ta_salamon: "சாலமன் பாப்பையா : துறைப் பெரியவர்களுக்கு அவர் விரும்புகின்றவற்றைச் செய்து, அவரைத் தமக்கு உரியவராகச் செய்து கொள்வது அரிய பேறுகளுள் எல்லாம் அரிது.",
    en: "To cherish great men and make them his own, is the most difficult of all difficult things."
