    ta_salamon: "சாலமன் பாப்பையா : தன்னைச் சூழ இருப்பவரைக் கண்ணாகக் கொண்டு அரசு இயங்குவதால் அப்படியே சூழும் துறைப் பெரியவரையே துணையாகக் கொள்க.",
    en: "As a king must use his ministers as eyes (in managing his kingdom), let him well examine their character and qualifications before he engages them."
