    ta_salamon: "சாலமன் பாப்பையா : தகுதி மிக்க துறைப்பெரியவரை நட்பாகக் கொண்டு, அவர் காட்டும் வழியில் நடப்பவரைப் பகைவர் ஏதும் செய்ய இயலாது.",
    en: "There will be nothing left for enemies to do, against him who has the power of acting (so as to secure) the fellowship of worthy men."
