    ta_salamon: "சாலமன் பாப்பையா : தீயன கண்டபோது கடிந்து சொல்லும் துறைப் பெரியவரைத் துணையாகக் கொள்ளாத பாதுகாப்பு அற்ற அரசு, அதைக் கெடுப்பார் இல்லாமலேயே தானாகவே கெடும்.",
    en: "The king, who is without the guard of men who can rebuke him, will perish, even though there be no one to destroy him."
