    ta_salamon: "சாலமன் பாப்பையா : மக்களுக்கு இயல்பான அறிவு அவர்தம் மனததால் உண்டாகும்; ஆனால், ஒருவன் இப்படிப்பட்டவன் என்று பெரியோர் சொல்லும் சொல் அவன் சார்ந்த இனம் காரணமாகவே உண்டாகும்.",
    en: "The power of knowing is from the mind; (but) his character is from that of his associates."
