    ta_salamon: "சாலமன் பாப்பையா : ஒரு செயலை நம்மிடம் செய்யக் கொடுத்தால் அச்செயலின் நன்மை தீமை இரண்டையும் ஆராய்ந்து எது நன்மையோ அதையே செய்ய வேண்டும்.",
