    ta_salamon: "சாலமன் பாப்பையா : நெடுங்காலம் ஆட்சி செய்ய விரும்புபவர் தண்டிக்கும்போது கடுமையாகத் தண்டிப்பவர்போல தொடங்கி வரம்பு கடவாமல் செய்க.",
    en: "Let the king, who desires that his prosperity may long remain, commence his preliminary enquires with strictness, and then punish with mildness."
