    ta_salamon: "சாலமன் பாப்பையா : குடிமக்கள் அஞ்சும்படியாகச் செயல் செய்யும் கொடிய ஆட்சி விரைந்து அழிவது உறுதி.",
    en: "The cruel-sceptred king, who acts so as to put his subjects in fear, will certainly and quickly come to ruin."
