    ta_salamon: "சாலமன் பாப்பையா : நம்மை ஆளுவோர் மிகவும் கொடியவர் என்று குடிமக்களால் சொல்லப்படும் கொடுஞ் சொல்லைப் பெற்ற ஆட்சி அதன் ஆட்சிக் காலம் குறைந்து விரைவி்ல் அழியும்.",
    en: "The king who is spoken of as cruel will quickly perish; his life becoming shortened."
