    ta_salamon: "சாலமன் பாப்பையா : தன்னைக் காண வருவார்க்கு நேரம் தருவதில் இழுத்தடிப்பும், கண்டால் முகக்கடுப்பும் உடையவரின் பெருஞ்செல்வம், பூதத்தால் கைக்கொள்ளப்பட்டது போன்றதாம்.",
    en: "The great wealth of him who is dificult of access and possesses a sternness of countenance, is like that which has been obtained by a devil."
