    ta_salamon: "சாலமன் பாப்பையா : மக்கள் வாழ்க்கை கண்ணோட்டத்தால்தான் இயங்குகின்றது அக்கண்ணோட்டம் இல்லாதவர் வாழ்வது இப்பூமிக்கு பாரமே.",
