    ta_salamon: "சாலமன் பாப்பையா : பாடப்படும் பாடலுக்குப் பொருந்தவில்லை என்றால் ராகத்தால் என்ன பயன்? அதுபோல கண்ணோட்டம் இல்லை என்றால் கண்ணால்தான் என்ன பயன்?",
