    ta_salamon: "சாலமன் பாப்பையா : நீர்ப்பூக்களின் அடிக்காம்பின் நீளம் நீரின் அளவே. அது போல மக்களின் உயர்வும் அவர்களின் மன ஊக்கத்தின் அளவே.",
    en: "The stalks of water-flowers are proportionate to the depth of water; so is men’s greatness proportionate to their minds."
