    ta_salamon: "சாலமன் பாப்பையா : தன்மீது அம்புகள் புதைந்து புண்பட்டபோதும் யானை தளராமல் தன் பெருமையை நிலைநிறுத்தும்; இதுபோல ஊக்கம் உடையவர் தமக்கு கேடு வந்த போதும் ஊக்கம் இழக்கமால் தம் பெருமையை நிலைநிறுத்துவர்.",
    en: "The strong minded will not faint, even when all is lost; the elephant stands firm, even when wounded by a shower of arrows."
