    ta_salamon: "சாலமன் பாப்பையா : ஊக்கம் இல்லாதவர் பிறர்க்கு உதவும் வள்ளல் யாம் என்னும் மன உயர்வைப் பெறமாட்டார்.",
    en: "Those who have no (greatness of) mind, will not acquire the joy of saying in the world, “we have excercised liaberality “."
