    ta_salamon: "சாலமன் பாப்பையா : யானை பெரிய உடம்பையும் கூர்மையான கொம்பினையும் உடையது என்றாலும் புலி தாக்கினால் பயப்படும்.",
    en: "Although the elephant has a large body, and a sharp tusk, yet it fears the attack of the tiger."
