    ta_salamon: "சாலமன் பாப்பையா : தன் உடலுக்கு இன்பம் வரும்போது அதை மனத்தால் விரும்பாதவன், துன்பம் வரும்போது மனம் தளர்ந்து துன்பப்படமாட்டான்.",
    en: "He does not suffer sorrow, in sorrow who does not look for pleasure in pleasure."
