    ta_salamon: "சாலமன் பாப்பையா : ஒரு செயலைச் செய்யத் தேவையான பொருள்கள், செய்வதற்கு ஏற்ற காலம், செய்யும் முறை, செய்யும் செயல் ஆகிய அனைத்திலும் நன்மை விளையும்படி எண்ணுபவரே அமைச்சர்.",
    en: "The minister is one who can make an excellent choice of means, time, manner of execution, and the difficult undertaking (itself)."
