    ta_salamon: "சாலமன் பாப்பையா : நாட்டிற்கு நெருக்கடி வரும்போது பகையானவரைப் பிரித்தல், தம்முடன் இருப்பவரைக் கொடையாலும் இன்சொல்லாலும் பிரியாமல் காத்தல், தேவைப்பட்டால் முன்னே நம்முடன் இருந்து பிரிந்தவரைச் சேர்த்துக் கொள்ளுதல் என்னும் இவற்றில் வல்லவரே அமைச்சர்.",
    en: "The minister is one who can efect discord (among foes), maintain the good-will of his friends and restore to friendship those who have seceded (from him)."
