    ta_salamon: "சாலமன் பாப்பையா : பல்வேறு மொழி நாட்டு நூல்களின் வழி, புதிய செயல் திறங்களை அறிந்திருந்தாலும், தன் நாட்டின் இயற்கை மக்கள் இயல்பு ஆகியவற்றை அறிந்து அவற்றிற்கு ஏற்பச் செய்க.",
    en: "Though you are acquainted with the (theoretical) methods (of performing an act), understand the ways of the world and act accordingly."
