    ta_salamon: "சாலமன் பாப்பையா : செயல்திறம் இல்லாத அமைச்சர், செய்ய வேண்டியவற்றை முறையாக எண்ணி வைத்திருந்தாலும், அவற்றைச் செய்யும்போது அரைகுறையாகவே செய்வார்.",
    en: "Those ministers who are destitute of (executive) ability will fail to carry out their projects, although they may have contrived aright."
