    number: 723,
    section: "பொருட்பால்",
    meaning: {
    ta_mu_va: "மு.வ : பகைவர் உள்ள போர்க்களத்தில் (அஞ்சாமல் சென்று) சாகத் துணிந்தவர் உலகத்தில் பலர், கற்றவரின் அவைக்களத்தில் பேச வல்லவர் சிலரே.",
    ta_salamon: "சாலமன் பாப்பையா : பகைவர்களுக்கிடையே பயப்படாமல் புகுந்து சாவோர் பலர் உண்டு;பேசுவோர் சிலரேயாவார்.",
    en: "Many indeed may (fearlessly) die in the presence of (their) foes; (but) few are those who are fearless in the assembly (of the learned)."
    }
