    ta_mu_va: "மு.வ : கற்றவரின் முன் தான் கற்றவைகளை அவருடைய மனதில் பதியுமாறு சொல்லி, மிகுதியாகக் கற்றவரிடம் அம்மிகுதியான கல்வியைக் அறிந்து கொள்ள வேண்டும்.",
    ta_salamon: "சாலமன் பாப்பையா : பலதுறை நூல்களையும் கற்றவர் அவையில், அவர்கள் மனங் கொள்ளுமாறு, தான் கற்றவற்றை எல்லாம் சொல்லுக; தான் கற்றவற்றிற்கும் மேலானவற்றை மிகவும் கற்றவரிடமிருந்து அறிந்து கொள்க.",
    en: "(Ministers) should agreeably set forth their acquirements before the learned and acquire more (know ledge) from their superiors (in learning)."
