    ta_salamon: "சாலமன் பாப்பையா : பெரியோர் அவையில் பயப்படாமல் பதில் சொல்வதற்கு, சொல்இலக்கண வழியில் பலவகைப் பிரமாணங்களைச் சொல்லும் தர்க்க சாஸ்திரத்தை விரும்பிக் கற்றுக் கொள்க.",
    en: "In order to reply fearlessly before a foreign court, (ministers) should learn logic according to the rules (of grammar)."
