    ta_salamon: "சாலமன் பாப்பையா : நெஞ்சுறுதி இல்லாதவர்க்கு வாளால் என்ன பயன்? அறிவுத்திறம் மிக்க அவைகண்டு பயப்படுபவர்க்குத் தர்க்க சாஸ்திர நூலால் பயன் என்ன?",
    en: "What have they to do with a sword who are not valiant, or they with learning who are afraid of an intelligent assembly ?"
