    ta_salamon: "சாலமன் பாப்பையா : நூல்களைக் கற்றும் அவற்றின் பயனை அறிந்தும், நல்லவர் கூடிய அவையைக் கண்டு பயந்து அங்கே செல்லாதவர், படிக்காதவரைவிடக் கீழானவர் என்று சொல்லுவர்.",
