    ta_salamon: "சாலமன் பாப்பையா : அவையைப் பார்த்துப் பயந்து, படித்தவற்றை அவைக்கு ஏற்பச் சொல்லத் தெரியாதவர், வாழ்ந்தாலும் வாழாதவர்க்குச் சமமே.",
