    ta_salamon: "சாலமன் பாப்பையா : பகைவரைச் சினந்து பார்க்கும் கண், அவர்கள் எறியும் வேலைப் பார்த்து மூடித் திறந்தாலும், சிறந்த வீரர்க்கு அதுவே புறங் கொடுத்தலாகும்.",
    en: "Is it not a defeat to the valiant to wink and destroy their ferocious look when a lance in cast at them (by their foe) ?"
