    ta_salamon: "சாலமன் பாப்பையா : ஒரு வீரன் தன் கடந்த நாள்களை எண்ணி எடுத்து, அவற்றுள் முகத்திலும் மார்பிலும் போரின்போது புண்படாத நாள்களைப் பயனில்லாமல் கழிந்த நாள்களாகக் கருதுவான்",
    en: "The hero will reckon among wasted days all those on which he had not received severe wounds."
