    ta_salamon: "சாலமன் பாப்பையா : தம்முடன் சொர்க்கத்திற்கு வராது பூமியில் மட்டு் நிலைத்து நிற்கும் புகழை விரும்பி, உயிரையும் விரும்பாத வீரர், தம் கால்களில் வீரக்கழலைக் கட்டுவது அவர்க்கு அழகே.",
    en: "The fastening of ankle-ring by those who disire a world-wide renown and not (the safety of) their lives is like adorning (themselves)."
