    ta_salamon: "சாலமன் பாப்பையா : அறிவில்லாதவரோடு கொண்ட நட்பை விட்டு விடுவது, ஒருவனுக்கு இலாபம்.",
    en: "It is indead a gain for one to renounce the friendship of fools."
