    ta_salamon: "சாலமன் பாப்பையா : கெடும்போது நம்மைக் கைவிட்டவரின் நட்பை நாம் சாகின்ற போது நினைத்தாலும் நம் நெஞ்சம் சுடும்.",
    en: "The very thought of the friendship of those who have deserted one at the approach of adversity will burn one’s mind at the time of death."
