    ta_salamon: "சாலமன் பாப்பையா : பெருமைக்கு உரியவர்களைப் பின்பற்றி அவர் மரபைக் காப்போம் என்னும் நல்லெண்ணம் சிறியவர் மனத்துள் இராது.",
    en: "It is never in the nature of the base to seek the society of the great and partake of their nature."
