    ta_salamon: "சாலமன் பாப்பையா : பணம், படிப்பு, பதவி ஆகிய சிறப்புகள் சிறுமைக்குணம் உடையவரிடம் சேர்ந்தால், அவர்களின் செயல்கள் அகங்காரத்தோடு வருவனவாம்.",
    en: "Even nobility of birth, wealth and learning, if in (the possession of) the base, will (only) produce everincreasing pride."
