    ta_salamon: "சாலமன் பாப்பையா : பெருமை உடையவர் செருக்கு இல்லாமல் பணிவுடன் இருப்பர்; சிறுமை உடையவரோ செருக்குடன் தம்மைத் தாமே வியந்து பாராட்டுவர்.",
