    ta_salamon: "சாலமன் பாப்பையா : பெருமைப்பட்டுக் கொள்ளக் காரணங்கள் இருந்தும் செருக்கு இல்லாமல் இருப்பது பெருமை; காரணம் இல்லா‌மலேயே பெருமைப்பட்டுக் கொள்வது சிறுமை.",
