    ta_salamon: "சாலமன் பாப்பையா : நாம் செய்யத்தக்க கடமை இது என்று சான்றாண்மையை மேற்கொண்டு வாழ்பவர்க்கு, நல்ல குணங்கள் எல்லாம் இயல்பாக இருக்கும் என்று கூறுவர்.",
    en: "It is said that those who are conscious of their duty and behave with a perfect goodness will regard as natu ral all that is good."
