    ta_salamon: "சாலமன் பாப்பையா : எமன் என்று நூலோர் சொல்ல முன்பு கேட்டிருக்கிறேன்; பார்த்தது இல்லை; இப்போது தெரிந்து கொண்டேன். பெண்ணிற்கே உரிய நல்ல குணங்களுடன் பெரிதாய்ப் போரிடும் கண்களையும் உடையது தான் எமன்.",
    en: "I never knew before what is called Yama; I see it now; it is the eyes that carry on a great fight with (the help of) female qualities."
