    ta_salamon: "சாலமன் பாப்பையா : பெண்மைக் குணம் மிக்க இப்பெண்ணின் கண்களுக்கு அவற்றைப் பார்ப்பவர் உயிரைப் பறிக்கும் தோற்றம் இருப்பதால் அவள் குணத்திற்கும் அறிவிற்கும் மாறுபட்டு போர் செய்கின்றன.",
    en: "These eyes that seem to kill those who look at them are as it were in hostilities with this feminine simplicity."
