    ta_salamon: "சாலமன் பாப்பையா : என்னை துன்புறுத்துவது எமனா? என் மேனி எங்கும் படர்வதால் கண்ணா? ஏதோ ஒரு பயம் தெரிவதால் பெண்மானா? இப்பெண்ணின் பார்வை இம்மூன்று குணங்களையும் பெற்றிருக்கிறது.",
    en: "Is it Yama, (a pair of) eyes or a hind ?- Are not all these three in the looks of this maid ?"
