    ta_salamon: "சாலமன் பாப்பையா : அதோ வளைந்து இருக்கும் புருவங்கள் வளையாமல் நேராக நின்று தடுத்தால், அவள் கண்கள், எனக்கு நடுக்கம் தரும் துன்பத்தை தரமாட்டா.",
    en: "Her eyes will cause (me) no trembling sorrow, if they are properly hidden by her cruel arched eye-brows."
