    ta_salamon: "சாலமன் பாப்பையா : அந்தப் பெண்ணின் சாயாத முலைமேல் இருக்கும் சேலை, கொல்லம் மதம் பிடித்த ஆண் யானையின் முகபடாம் போன்று இருக்கிறது.",
    en: "The cloth that covers the firm bosom of this maiden is (like) that which covers the eyes of a rutting elephant."
