    ta_salamon: "சாலமன் பாப்பையா : பெண்மானைப் போன்ற அச்சப் பார்வையையும் உள்ளத்தில் நாணத்தையும் நகைகளாகக் கொண்டிருக்கும் இவளுக்கு வேறு வேறு வகைப்பட்ட நகைகளை அணிவித்திருப்பது எதற்காகவோ?",
    en: "Of what use are other jewels to her who is adorned with modesty, and the meek looks of a hind ?"
