    ta_salamon: "சாலமன் பாப்பையா : இவளின் மையூட்டப்பட்ட கண்களில் என்மேல் இரண்டு நோக்கம் இருப்பது தெரிகிறது. ஒரு நோக்கம் எனக்கு துன்பம் தெரிகிறது. மற்றொன்று அந்தத் துன்பத்திற்கு மருந்து ஆகிறது.",
    en: "There are two looks in the dyed eyes of this (fair one); one causes pain, and the other is the cure thereof."
