    ta_salamon: "சாலமன் பாப்பையா : நான் பார்க்காதபோது, என்னைப் பார்த்தாள்; பார்த்து நாணத்தால் தலைகுனிந்தாள்; இந்த செயல் எங்களுக்குள் காதல் பயிர் வளர அவள் ஊற்றிய நீராகும்.",
    en: "She has looked (at men) and stooped (her head); and that (sign) waters as it were (the corn of) our love."
