    ta_salamon: "சாலமன் பாப்பையா : நான் அவளை பார்க்கும்போது தலைகுனிந்து நிலத்தைப் பார்ப்பாள், நான் பார்க்காதபோதோ என்னைப் பார்த்து மெல்ல தனக்குள்ளே சிரிப்பாள்.",
    en: "When I look, she looks down; when I do not, she looks and smiles gently."
