    ta_salamon: "சாலமன் பாப்பையா : நேரே பார்க்காமல் ஒரு கண்ணை மட்டும் சுருக்கி பார்ப்பவள் போல என்னைப் பார்த்துப் பார்த்துப் பிறகு தனக்குள் தானே மகிழ்வாள்.",
    en: "She not only avoids a direct look at me, but looks as it were with a half-closed eye and smiles."
