    ta_salamon: "சாலமன் பாப்பையா : முன்பின் தெரியாதவர் போல, பொதுவாக பார்த்தப் பேசுவது காதலர்களிடம் இருக்கும் குணந்தான்.",
    en: "Both the lovers are capable of looking at each other in an ordinary way, as if they were perfect strangers."
