    ta_salamon: "சாலமன் பாப்பையா : இறுக அணைத்துக் கிடப்பதால் காற்றும் ஊடே நுழைய முடியாதபடி கூடிப் பெறும் சுகம், விரும்பிக் காதலிப்பார் இருவர்க்கும் இனிமையானதே."
