    ta_salamon: "சாலமன் பாப்பையா : என்னிடம் மெல்லிதாகப் பேசும் என் மனைவியின் வெண்மையான பற்களிடையே ஊறிய நீர், பாலோடு தேனைக் கலந்த கலவை போலும்!",
    en: "The water which oozes from the white teeth of this soft speeched damsel is like a mixture of milk and honey."
