    ta_salamon: "சாலமன் பாப்பையா : என் மனைவிக்கும் எனக்கும் இடையே உள்ள உறவு, உடம்பிற்கும் உயிருக்கும் இடையே எத்தகைய உறவோ அத்தகையது.",
    en: "The love between me and this damsel is like the union of body and soul."
