    ta_salamon: "சாலமன் பாப்பையா : என் கருமணிக்குள் இருக்கும் பாவையே! நீ அதை விட்டுப் போய்விடு; நான் விரும்பும் என் மனைவிக்கு என் கண்ணுக்குள் இருக்க இடம் போதவில்லை.",
    en: "O you image in the pupil (of my eye)! depart; there is no room for (my) fair-browed beloved."
