    ta_mu_va: "மு.வ : ஆராய்ந்து அணிகலன்களை அணிந்த இவள் கூடும் போது உயிர்க்கு வாழ்வு போன்றவள், பிரியும் போது உயிர்க்கு சாவு போன்றவள்.",
    ta_salamon: "சாலமன் பாப்பையா : என் மனைவி, நான் அவளுடன் கூடும்போது உயிருக்கு உடம்பு போன்றிருக்கிறாள். அவளைப் பிரியும்போது உயிர் உடம்பை விட்டுப் பிரிவது போன்றிருக்கிறாள்.",
    en: "My fair-jewelled one resembles the living soul (when she is in union with me), the dying soul when she leaves me."
