    ta_salamon: "சாலமன் பாப்பையா : ஒளியுடன் கூடிய கண்களை உடைய என் மனைவியின் குணங்களை நான் மறந்தால் அல்லவா அவளை நினைப்பதற்கு? மறப்பதும் இல்லை. அதனால் நினைப்பதும் இல்லை.",
    en: "If I had forgotten her who has bright battling eyes, I would have remembered (thee); but I never forget her. (Thus says he to her maid)."
