    ta_salamon: "சாலமன் பாப்பையா : என் அன்பர் என் கண்ணை விட்டுப் போகமாட்டிடார்; ஒருவேளை நான் அறியாமல் இமைத்தால் வருந்தவும் மாட்டார். பிறர் அறிய முடியாத நுட்பத் தன்மையர் அவர்.",
    en: "My lover would not depart from mine eyes; even if I wink, he would not sufer (from pain); he is so ethereal."
