    ta_salamon: "சாலமன் பாப்பையா : காதல் நிறைவேற முடியாமல் வருந்தும் காதலர்க்கு மடல் ஏறுதலைத் தவிர வேறு பலம் இல்லை.",
    en: "To those who after enjoyment of sexual pleasure suffer (for want of more), there is no help so eficient as the palm yra horse."
