    ta_salamon: "சாலமன் பாப்பையா : காதலை நிறைவேற்ற முடியாது வருந்தும் இந்த உடலும் உயிரும் வெட்கத்தை விட்டுவிட்டு மடல் ஏற எண்ணுகின்றன."
