    ta_salamon: "சாலமன் பாப்பையா : அவள் குணத்தை எண்ணி என் கண்கள் இரவெல்லாம் உறங்குவதில்லை. அதனால் நள்ளிரவிலும்கூட மடல் ஊர்வது பறிறயே எண்ணுவேன்.",
