    ta_mu_va: "மு.வ : இந்தக் காம நோய் ஊராரின் அலர் தூற்றலே எருவாகவும் அன்னை கடிந்து சொல்லும் கடுஞ்சொல்லே நீராகவும் கொண்டு செழித்து வளர்கின்றது.",
    ta_salamon: "சாலமன் பாப்பையா : இந்த ஊர்ப் பெண்கள் பேசும் பேச்சே உரமாக தாயின் தடைச்சொல் நீராக என் காதல் பயிர் வளரும்.",
    en: "This malady (of lust) is manured by the talk of women and watered by the (harsh) words of my mother."
