    ta_salamon: "சாலமன் பாப்பையா : இந்த ஊரார் தங்கள் அலரால் எங்கள் காதலை அழித்து விடுவோம் என்று எண்ணுவது, நெய்யை ஊற்றியே நெருப்பை அணைப்போம் என்பது போலாம்.",
    en: "To say that one could extinguish passion by rumour is like extinguishing fire with ghee."
