    ta_salamon: "சாலமன் பாப்பையா : நான் விரும்பிய அவரைப் பற்றித்தான் இவ்வூர் பேசுகிறது. இனி என் காதலரும் நான் விரும்பியபோது என்னைத் திருமணம் செய்வார்.",
    en: "The rumour I desire is raised by the town (itself); and my lover would if desired consent (to my following him)."
