    ta_salamon: "சாலமன் பாப்பையா : எல்லாம் அறியும் ஆற்றல் உடைய அவரும் ஒருநேரம் பிரிவார் என்றால், என்மீது அவர் கொண்டிருக்கும் அன்பை அறிந்து கொள்ள முடியவில்லை.",
