    ta_salamon: "சாலமன் பாப்பையா : என்னை மணந்தபோதே என்மீது அன்பு காட்டிப் பயப்படாதே, உன்னைப் பிரியமாட்டேன் என்று சொல்லி என்னைத் தேற்றிய அவர் சொல்லை, நான் நம்பியது தவறோ?",
