    ta_salamon: "சாலமன் பாப்பையா : என் உயிரைக் காக்க எண்ணினால் அதைக் காப்பதற்கு உரிய அவர், என்னை விட்டுப் பிரிவதைத் தவிர்க்க வேண்டும். மீறிப் பிரிந்தால் நான் இனி அவரைச் சேர்வது அரிது.",
