    ta_salamon: "சாலமன் பாப்பையா : நான் வேலையின் பொருட்டுப் பிரியப் போகிறேன் என்று அவரே என்னிடம் சொல்லும் அளவிற்குக் கொடியவர் என்றால், அவர் பிரிவைத் தாங்க முடியாத என் மீது அன்பு காட்டுவார் என்னும் என் எதிர்பார்ப்பு பயனற்றது.",
