    ta_salamon: "சாலமன் பாப்பையா : அவர் என்னைப் பிரிய திட்டமிடுகிறார் என்பதை என் முன் கையிலிருந்து கழலும் வளையல்கள் எனக்குத் தெரிவிக்க மாட்டாவோ?",
    en: "Do not the rings that begin to slide down my fingers forebode the separation of my lord ?"
