    ta_salamon: "சாலமன் பாப்பையா : தம் நெஞ்சத்தில் என்னை விலக்கிவிட்ட அவர், என் நெஞ்சத்தில் மட்டும் ஓயாமல் வருவதற்கு வெட்கப் படமாட்டாரோ?",
    en: "He who has imprisoned me in his soul, is he ashamed to enter incessantly into mine."
