    ta_salamon: "சாலமன் பாப்பையா : கண்ணால் காண்பது என்றொரு கொடிய பாவி இல்லை என்றால் கனவிலே வந்து கூடிய என்னவர் என்னைப் பிரிய மாட்டார்.",
    en: "Were there no such thing as wakefulness, my beloved (who visited me) in my dream would not depart from me."
