    ta_salamon: "சாலமன் பாப்பையா : நேரில் வந்து அன்பு செய்யாத இந்தக் கொடிய மனிதர் கனவில் மட்டும் நாளும் வந்து என்னை வருத்துவது ஏன்?",
    en: "The cruel one who would not favour me in my wakefulness, what right has he to torture me in my dreams?"
