    ta_salamon: "சாலமன் பாப்பையா : பொழுதே! நீ வாழ்க! முன்பெல்லாம் வருவாயே அந்த மாலையா நீ என்றால் இல்லை; திருமணம் செய்து கொண்ட பெண்களின் உயிரை வாங்கும் பொழுது நீ.",
    en: "Live, O you evening are you (the former) evening? No, you are the season that slays (married) women."
