    ta_salamon: "சாலமன் பாப்பையா : பகலும் இரவுமாய் மயங்கும் மாலைப்பொழுதே! என்னைப் போலவே நீயும் ஒளி இழந்த கண்ணோடு இருக்கிறாயே; உன் கணவரும் என் கணவரைப் போல் கொடியவரோ?",
    en: "A long life to you, O dark evening! You are sightless. Is your help-mate (also) as hard-hearted as mine."
