    ta_salamon: "சாலமன் பாப்பையா : அவர் என்னைப் பிரிவதற்கு முன்பு என்முன் வரவே நடுக்கம் எய்தி மேனி கறுத்து வந்த இந்த மாலைப் பொழுது இப்போது எனக்குச் சாவு வரும்படி தோன்றி, அதற்கான துன்பம் பெருகும்படி நாளும் வருகின்றது.",
    en: "The evening that (once) came in with trembling and dimness (now) brings me an aversion for life and increasing sorrow."
