    ta_mu_va: "மு.வ : யான் காலைப்பொழுதிற்குச் செய்த நன்மை என்ன? (என்னைத் துன்புறுத்துகின்ற) மாலைப் பொழுதிற்குச் செய்த பகையான தீமை என்ன?",
    ta_salamon: "சாலமன் பாப்பையா : காலைக்கு நான் செய்த நன்மை என்ன? மாலைக்கு நான் செய்த தீமை என்ன?",
    en: "What good have I done to morning (and) what evil to evening?"
