    ta_salamon: "சாலமன் பாப்பையா : முன்பு எனக்கு மகிழ்ச்சி தந்த மாலைப்பொழுது இப்படித் துன்பம் தரும் என்பதை, என்னை மணந்த காதலர் என்னைப் பிரிவதற்கு முன்பு நான் அறிந்தது கூட இல்லை.",
    en: "Previous to my husband’s departure, I know not the painful nature of evening."
