    ta_salamon: "சாலமன் பாப்பையா : முன்பு இனிதாய் ஒலித்த ஆயனின் புல்லாங்குழல் இப்போது நெருப்பாய்ச் சுடும் மாலைப் பொழுதிற்கு் தூதானது மட்டும் அன்றி, என்னைக் கொல்லும் ஆயுதமுமாகிவிட்டது.",
