    ta_salamon: "சாலமன் பாப்பையா : பிரிவைப் பொறுக்காத சிறுமை என்னோடு இருக்கப் பிரிவைப் பொறுத்துக் கொண்டு தொலைவில் சென்று அவரை எண்ணி அழுவதால், கண்கள் ஒளி இழந்துவிட்டன. முன்பு கண்களைக் கண்டு வெட்கப்பட்ட மண மலர்களுக்கு இப்போது கண்கள் வெட்கப்பட்டுவிட்டன.",
