    ta_salamon: "சாலமன் பாப்பையா : நெஞ்சே! கொடுமையானவராகிய அவரிடம் சென்று என் மெலியும் தோள்களினால் ஏற்பட்டுள்ள வெற்றுரைகளைச் சொல்லி நீ பெருமை பெறுவாயோ?",
    en: "Can you O my soul! gain glory by relating to the (so-called) cruel one the clamour of my fading shoulders?"
