    ta_salamon: "சாலமன் பாப்பையா : நாணம் என்னும் தாழ்பாளைக் கோத்திருக்கும் நிறை எனப்படும் கதவைக் காதல் விருப்பமாகிய கோடரி பிளக்கின்றதே!",
    en: "The axe of lust can break the door of chastity which is bolted with the bolt of modesty."
