    ta_salamon: "சாலமன் பாப்பையா : தன்னைப் பிரிந்து சென்றவர் பின்னே செல்லாது, தானும் அவரை விட்டுப் பிரிந்து நிற்கும் மன அடக்கத்தைக் காதல் நோயை அறியாதவர் பெற முடியும். அறிந்தவரால் பெற முடியாது.",
    en: "The dignity that would not go after an absent lover is not known to those who are sticken by love."
