    ta_mu_va: "மு.வ : வெறுத்து நீங்கிய காதலரின் பின் செல்ல விரும்பிய நிலையில் இருப்பதால் என்னை அடைந்த இந்த காமநோய் எத்தன்மையானது? அந்‌தோ!",
    ta_salamon: "சாலமன் பாப்பையா : என்னைப் பிரிந்து சென்றவர் பின்னே நான் போய்ச் சேர வேண்டும் என்று என்னைப் பிடித்த இந்தக் காதல் நோய் தூண்டுவதால் இது மிகமிகக் கொடியது.",
    en: "The sorrow I have endured by desiring to go after my absent lover, in what way is it excellent?"
