    ta_salamon: "சாலமன் பாப்பையா : தொலைதூரம் சென்று தன் கணவன் வரும் நாளை எண்ணி வருந்தும் பெண்களுக்கு ஒருநாள் பலநாள் போல நெடிதாகத் தோன்றும்.",
    en: "To those who sufer waiting for the day of return of their distant lovers one day is as long as seven days."
